import React from "react";
import Carousel from "react-material-ui-carousel";
import { Button } from "@mui/material";
import Airplane from "../img/Airplane-beneath.jpg";
// import Cargo from "../img/Containers.jpg";
import Ship from "../img/Ship-shipment.jpg";
import Bike from "../img/bike.jpg";
import "./Slider.css";

const Slider = (props) => {
  var items = [
    {
      name: "Air Frieght",
      src: Airplane,
      alt: "airplane img",
    },
    {
      name: "Sea Frieght",
      src: Ship,
      alt: "Ship img",
      //   description: "Hello World!",
    },
    {
      name: "Logistics",
      src: Bike,
      alt: "bike img",
    },
  ];

  return (
    <div className="slider-container" id="services">
      <div className="slider-header">
        <h1>Our Services</h1>
      </div>
      <div className="slider-content">
        <Carousel
          navButtonsProps={{
            // Change the colors and radius of the actual buttons. THIS STYLES BOTH BUTTONS
            style: {
              backgroundColor: "orangered",
              borderRadius: 50,
            },
          }}
        >
          {items.map((item, i) => (
            <Item key={i} item={item} />
          ))}
          {/* <div className="slider-img">
          <img src={Airplane} alt="airplane img"></img>
          <img src={Cargo} alt="cargo img"></img>
        </div> */}
        </Carousel>
      </div>
    </div>
  );
};
export default Slider;

function Item(props) {
  return (
    <div className="slider-paper">
      {/* <p>{props.item.description}</p> */}
      <div className="slider-img">
        <img src={props.item.src} alt={props.item.alt}></img>
        <div className="slider-details">
          <h2>{props.item.name}</h2>
          <Button variant="outlined" size="medium" className="CheckButton">
            Check it out!
          </Button>
        </div>
      </div>
    </div>
  );
}
