import React from "react";
import "./Footer.css";
import { Button } from "@mui/material";
import { Link } from "react-router-dom";
import { styled } from "@mui/system";
import MuiTextField from "@mui/material/TextField";

function Footer({ pageUp }) {
  const options = {
    shouldForwardProp: (prop) => prop !== "borderColor",
  };

  const outlinedSelectors = [
    "& .MuiOutlinedInput-notchedOutline",
    "&:hover .MuiOutlinedInput-notchedOutline",
    "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline",
  ];
  const TextField = styled(
    MuiTextField,
    options
  )(({ borderColor, color }) => ({
    "& label.Mui-focused": {
      color: borderColor,
      border: color,
    },
    [outlinedSelectors.join(",")]: {
      borderWidth: 3,
      borderColor,
    },
  }));

  return (
    <div className="footer-container" id="footer">
      <section className="footer-subscription">
        <p className="footer-subscription-heading">
          Join the newsletter to receive our best deals/discounts
        </p>
        <p className="footer-subscription-text">
          You can unsubscribe at any time.
        </p>
        <div>
          <form className="form-footer">
            <TextField
              id="outlined-basic"
              label="Your E-mail"
              type="email"
              variant="outlined"
              className="footer-input"
              borderColor="white"
            />
            <Link to="" className="btn-footer">
              <Button variant="outlined" size="medium" type="submit">
                Subscribe
              </Button>
            </Link>
          </form>
        </div>
      </section>
      <div className="footer-links">
        <div className="footer-link-wrapper">
          <div className="footer-link-items">
            <h2>About Us</h2>
            <Link to="/sign-up">How it works</Link>
            <Link to="/">Testimonials</Link>
            <Link to="/">Careers</Link>
            <Link to="/">Investors</Link>
            <Link to="/">Terms of Service</Link>
          </div>
          <div className="footer-link-items">
            <h2>Contact Us</h2>
            <Link to="/">Contact</Link>
            <Link to="/">Support</Link>
            <Link to="/">Destinations</Link>
            <Link to="/">Sponsorships</Link>
          </div>
        </div>
        <div className="footer-link-wrapper">
          <div className="footer-link-items">
            <h2>Videos</h2>
            <Link to="/">Submit Video</Link>
            <Link to="/">Ambassadors</Link>
            <Link to="/">Agency</Link>
            <Link to="/">Influencer</Link>
            <Link to="/">Pop-up Ads</Link>
          </div>
          <div className="footer-link-items">
            <h2>Social Media</h2>
            <Link to="/">Instagram</Link>
            <Link to="/">Facebook</Link>
            <Link to="/">Youtube</Link>
            <Link to="/">Twitter</Link>
          </div>
        </div>
      </div>
      <section className="social-media">
        <div className="social-media-wrap">
          <div className="footer-logo">
            <Link to="/" onClick={pageUp} smooth="true" className="social-logo">
              <ion-icon name="flash"></ion-icon>

              <div>
                <i>Flash </i> Couriers
              </div>
            </Link>
          </div>
          <small className="website-rights">
            FC Â© {new Date().getFullYear()}
          </small>
          <div className="social-icons">
            <Link
              className="social-icon-link facebook"
              to="/"
              target="_blank"
              aria-label="Facebook"
            >
              <i className="fab fa-facebook-f" />
            </Link>
            <Link
              className="social-icon-link instagram"
              to="/"
              target="_blank"
              aria-label="Instagram"
            >
              <i className="fab fa-instagram" />
            </Link>
            <Link
              className="social-icon-link youtube"
              to="/"
              target="_blank"
              aria-label="Youtube"
            >
              <i className="fab fa-youtube" />
            </Link>
            <Link
              className="social-icon-link twitter"
              to="/"
              target="_blank"
              aria-label="Twitter"
            >
              <i className="fab fa-twitter" />
            </Link>
            <Link
              className="social-icon-link twitter"
              to="/"
              target="_blank"
              aria-label="LinkedIn"
            >
              <i className="fab fa-linkedin" />
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;
