import "./Navbar.css";

import React, { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import Button from "@mui/material/Button";
import { Link as LinkS } from "react-scroll";

function Navbar({ pageUp }) {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 1000) {
      setButton(false);
    } else {
      setButton(true);
    }
  };
  const isHome = useLocation().pathname === "/";

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener("resize", showButton);

  return (
    <>
      <nav className="navbar">
        <div className="navbar-container">
          <Link to="/" className="navbar-logo" onClick={pageUp}>
            <div className="nav-logo">
              <ion-icon name="flash"></ion-icon>
            </div>
            <div>
              <i>Flash</i> Couriers
            </div>
          </Link>
          <div className="menu-icon" onClick={handleClick}>
            {/* {click ? "menu" : "close"} */}
            {/* <i className={click ? "fas fa-times" : "fas fa-bars"} /> */}
            <ion-icon
              className="navicon"
              name={click ? "close-outline" : "menu-outline"}
            ></ion-icon>
          </div>
          <ul className={click ? "nav-menu active" : "nav-menu"}>
            <li className="nav-item">
              <Link
                to="/"
                className="nav-links"
                onClick={() => {
                  closeMobileMenu();
                  pageUp();
                }}
              >
                Home
              </Link>
            </li>
            {isHome && (
              <li className="nav-item">
                <LinkS
                  to="track"
                  className="nav-links"
                  onClick={closeMobileMenu}
                  smooth="true"
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={-80}
                >
                  Track
                </LinkS>
              </li>
            )}
            {isHome && (
              <li className="nav-item">
                <LinkS
                  to="services"
                  className="nav-links"
                  onClick={closeMobileMenu}
                  smooth="true"
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={-80}
                >
                  Services/FAQs
                </LinkS>
              </li>
            )}
            {isHome && (
              <li className="nav-item">
                <LinkS
                  to="highlights"
                  className="nav-links"
                  onClick={closeMobileMenu}
                  smooth="true"
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={-80}
                >
                  Highlights
                </LinkS>
              </li>
            )}
            <li className="nav-item">
              <LinkS
                to="footer"
                className="nav-links"
                onClick={closeMobileMenu}
                smooth="true"
                duration={500}
                spy={true}
                exact="true"
                offset={-80}
              >
                Contact Us
              </LinkS>
            </li>
            <li className="nav-item">
              {isHome && (
                <Link
                  to="/sign-in"
                  className="nav-links-mobile"
                  onClick={() => {
                    closeMobileMenu();
                    pageUp();
                  }}
                >
                  Sign In
                </Link>
              )}
            </li>
          </ul>
          {button && isHome && (
            <Link to="sign-in" className="btn-nav" onClick={pageUp}>
              <Button
                /* {["btn-nav", "nav-item"]} */
                variant="outlined"
                size="medium"
              >
                Sign in
              </Button>
            </Link>
          )}
        </div>
      </nav>
    </>
  );
}

export default Navbar;
