import "./HeroSection.css";
// import video from "../videos/video.mp4";
import PlayCircleFilledWhiteRoundedIcon from "@mui/icons-material/PlayCircleFilledWhiteRounded";
import worldSvg from "../img/svgs/connected_world_wuay.svg";
import Button from "@mui/material/Button";

function HeroSection() {
  return (
    <div className="hero-container">
      <div className="hero-content">
        {/* <video src={video} autoPlay loop muted /> */}
        <h1>CONNECT YOUR WORLDS</h1>
        <p>...take down the distance between</p>
        <div className="hero-btns">
          <Button className="btn-outline" variant="outlined" size="large">
            GO!
          </Button>
          <Button
            className="btn-primary"
            variant="contained"
            size="large"
            endIcon={<PlayCircleFilledWhiteRoundedIcon />}
          >
            Learn More
          </Button>
        </div>
      </div>
      <div className="hero-illustration">
        <img src={worldSvg} alt="world"></img>
        {/* <svg src={worldSvg}></svg> */}
      </div>
    </div>
  );
}

export default HeroSection;
