import { Button } from "@mui/material";
import React from "react";
import { CompareArrows, KeyboardArrowRightRounded } from "@mui/icons-material";

const Card = ({ heading, svg, alt, local, second }) => {
  return (
    <div className="card-box">
      <div className={"card-content " + (second ? "reverse" : "")}>
        <div className="card-img">
          <img src={svg} alt={alt}></img>
        </div>
        <div className="card-details">
          <div className="card-text">
            <h2>{heading}</h2>
          </div>
          <div className="card-btn">
            <Button
              className="btn-primary"
              variant="outlined"
              size="large"
              endIcon={<CompareArrows />}
            >
              Xpress
            </Button>

            <Button
              className="btn-primary"
              variant="outlined"
              size="small"
              endIcon={<KeyboardArrowRightRounded />}
            >
              {local ? "Inter-city Frieght" : "International frieght"}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card;
