import React, { useState } from "react";
import "./LogInForm.css";
import Button from "@mui/material/Button";
import { TextField } from "@mui/material";
import "./LogInForm.css";
import Fingerprint from "../img/svgs/fingerprint.svg";
import { Link } from "react-router-dom";

const LogInForm = ({ handleClose }) => {
  //   const [firstName, setFirstName] = useState("");
  //   const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log(firstName, lastName, email, password);
    console.log(email, password);
    handleClose();
  };

  return (
    <div className="form-container">
      <img src={Fingerprint} alt="secured img" className="form-bg"></img>
      <h2>Avoid Sharing your Login Credentials</h2>
      <form onSubmit={handleSubmit} className="form">
        <TextField
          label="Email"
          variant="outlined"
          type="email"
          required
          className="textfield"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <TextField
          label="Password"
          variant="outlined"
          type="password"
          className="textfield"
          required
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <div>
          <Button type="submit" variant="contained" color="warning">
            Sign In
          </Button>
        </div>
        <Link to="/sign-up" className="btn-nav">
          <div>
            <Button variant="text" onClick={handleClose} color="warning">
              Click here to create an account
            </Button>
          </div>
        </Link>
        <div>
          <Button variant="text" onClick={handleClose} color="warning">
            Forgot Password
          </Button>
        </div>
      </form>
    </div>
  );
};

export default LogInForm;
