import React from "react";
import "./Cards.css";
import Card from "./CardItem";
// import parcel from "../img/svgs/delivery.svg";
import drone from "../img/svgs/drone_delivery.svg";
import discount from "../img/svgs/discount.svg";
import wallet from "../img/svgs/wallet.svg";
import winners from "../img/svgs/winners.svg";

const Cards = () => {
  return (
    <div className="card-container" id="highlights">
      <h1>Highlights</h1>
      <div className="card-row">
        <Card
          heading="Unimaginable discounts for non-fragile items"
          svg={discount}
          alt="discount img"
          local={false}
        />
        <Card
          heading="Drone deliveries in remote areas"
          svg={drone}
          alt="Drone img"
          second
          local
        />
      </div>
      <div className="card-row">
        <Card
          heading="Save more with our Logistic price tags "
          svg={wallet}
          alt="Save more img"
        />
        <Card
          heading="Award Winners of the 2021 Frieght challenge"
          svg={winners}
          alt="Trophy img"
          second
        />
      </div>
    </div>
  );
};

export default Cards;
