// import { makeStyles } from "@material-ui/core";
// import TextField from "@material-ui/core/TextField";
// import Button from "@material-ui/core/Button";
import { TextField } from "@mui/material";
import Button from "@mui/material/Button";
import Boxes from "../img/svgs/deliveries.svg";
import "./SignUpForm.css";
import { Link } from "react-router-dom";

/* const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    padding: theme.spacing(2),

    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "300px",
    },
    "& .MuiButtonBase-root": {
      margin: theme.spacing(2),
    },
  },
})); */

const SignUp = ({ handleClose }) => {
  //   const classes = useStyles();
  // create state variables for each input
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(firstName, lastName, email, password);
    handleClose();
  };

  return (
    <div className="form-container">
      <img src={Boxes} alt="secured img" className="form-bg"></img>
      <h2>We're glad you're joining us</h2>
      <form onSubmit={handleSubmit} className="form signup">
        <TextField
          label="First Name"
          variant="outlined"
          required
          className="textfield"
          value={firstName}
          onChange={(e) => setFirstName(e.target.value)}
        />
        <TextField
          label="Last Name"
          variant="outlined"
          required
          className="textfield"
          value={lastName}
          onChange={(e) => setLastName(e.target.value)}
        />
        <TextField
          label="Email"
          variant="outlined"
          type="email"
          required
          className="textfield"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <TextField
          label="Password"
          variant="outlined"
          type="password"
          required
          className="textfield"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <div>
          <Button type="submit" variant="text" color="warning">
            Create Account
          </Button>
        </div>
        <div>
          <Link to="/sign-in" className="btn-nav">
            <Button variant="text" onClick={handleClose}>
              Cancel
            </Button>
          </Link>
        </div>
      </form>
    </div>
  );
};

export default SignUp;
